/*   
   http://nicolasgallagher.com/micro-clearfix-hack/
   */
.h-clearfix:before,
.h-clearfix:after {
	content: " ";
	display: table;
}
.h-clearfix:after {
	clear: both;
}


/*
	     . ,     
*/
.h-error-border {
	border: 1px solid #ff3333;
}


/*
	 ,     - 
*/
.h-canvas-freeze,
.h-canvas-freeze-for-touch-navi {
	width: 100%;
	height: 100%;
	overflow: hidden;
}


/**/
.h-hidden { display: none; }

/**/
@media @desktop {
	.h-desktop-hidden {}
	.h-desktop-showed {
		display: block;
	}
}


/**/
.h-touch-hidden {}
@media @touch {
	.h-touch-hidden {
		display: none;
	}
}

